<template>
  <div class="slider" @touchstart="sliderTouchStart" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(item,index) in dots" :class="{active: currentPage === index}"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from "better-scroll";
import { addClass } from "common/js/dom";
export default {
  data() {
    return {
      dots: [],
      currentPage: 0
    };
  },
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    scrollX: {
      type: Boolean,
      default: true
    },
    scrollY: {
      type: Boolean,
      default: false
    },
    click: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 2000
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    showDot: {
      type: Boolean,
      default: true
    },
    speed: {
      type: Number,
      default: 400
    },
    threshold: {
      type: Number,
      default: 0.3
    },
    momentum: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  methods: {
    _initSlider() {
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: this.scrollX,
        scrollY: this.scrollY,
        momentum: this.momentum,
        snap: {
          loop: this.loop,
          threshold: this.threshold,
          speed: this.speed
        },
        autoPlay: this.autoPlay,
        click: this.click
      });

      this.slider.on("scrollEnd", () => {
        let pageIndex = this.slider.getCurrentPage().pageX;
        // if(this.loop){
        //   pageIndex -= 2;
        // }
        this.currentPage = pageIndex;
        // console.log(pageIndex);

        if (this._play) {
          clearTimeout(this.time);
          this._play();
        }
      });
    },
    _setSliderWidth(isResize) {
      this.children = this.$refs.sliderGroup.children; // slider-group
      let width = 0; // slider-group
      let sliderWidth = this.$refs.slider.clientWidth; // slider
      console.log(this.children.length);

      for (let i = 0; i < this.children.length; i++) {
        // slider-group
        let child = this.children[i]; // slider-group
        addClass(child, "slider-item"); // class
        child.style.width = sliderWidth + "px"; // 
        width += sliderWidth; // 
      }
      if (this.loop && !isResize) {
        width += 2 * sliderWidth; // 2
      }
      this.$refs.sliderGroup.style.width = width + "px"; // slider-group
    },
    _initDot() {
      this.dots = new Array(this.children.length);
    },
    _play() {
      let pageIndex = this.currentPage + 1;
      if (this.loop) {
        pageIndex += 1;
      }
      this.time = setTimeout(() => {
        this.slider.next();
      }, this.interval);
    },
    sliderTouchStart(){
      console.log(1);
      clearTimeout(this.time);
    }
  },
  mounted() {
    setTimeout(() => {
      this._setSliderWidth();
      this._initDot();
      this._initSlider();
      if (this.autoPlay) {
        this._play();
      }
    }, 30);

    window.addEventListener("resize", () => {
      if (!this.slider) {
        return;
      }
      this._setSliderWidth(true);
    });
  },
  destroyed() {
    clearTimeout(this.time);
  },
  activated() {
    if (this.autoPlay) {
      this._play();
    }
  },
  deactivated() {
    clearTimeout(this.time);
  },
  beforeDestroy() {
    clearTimeout(this.time);
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';

.slider {
  min-height: 1px;

  .slider-group {
    position: relative;
    overflow: hidden;
    white-space: nowrap;

    .slider-item {
      float: left;
      box-sizing: border-box;
      overflow: hidden;
      text-align: center;

      a {
        display: block;
        width: 100%;
        overflow: hidden;
        text-decoration: none;
      }

      img {
        display: block;
        width: 100%;
      }
    }
  }

  .dots {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 12px;
    text-align: center;
    font-size: 0;

    .dot {
      display: inline-block;
      margin: 0 4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: $color-text-l;

      &.active {
        width: 20px;
        border-radius: 5px;
        background: $color-text-ll;
      }
    }
  }
}
</style>



