<template>
 <scroll class="suggest" ref="suggest" @scrollToEnd="searchMove" @beforeScroll="listScroll" :beforeScroll="beforeScroll" :data="result" :pullup="pullup">
   <ul class="suggest-list">
     <li class="suggest-item" @click="selectItem(item)" :key="index" v-for="(item,index) in result">
       <div class="icon">
         <i :class="getIconCls(item)"></i>
       </div>
       <div class="name">
         <p class="text" v-html="getDiscplayName(item)"></p>
       </div>
     </li>
     <loading v-show="hasMove"></loading>
   </ul>
   <div class="no-result-wrapper" v-show="!hasMove && !result.length">
     <no-result title=""></no-result>
   </div>
 </scroll>
</template>

<script type="text/ecmascript-6">
import { search } from "api/search";
import { ERR_OK } from "api/config";
import { createSong } from "common/js/song";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import Singer from "common/js/singer";
import { mapMutations, mapActions } from "vuex";
import noResult from "base/no-result/no-result";
const TYPE_SINGER = "singer";
export default {
  data() {
    return {
      page: 1,
      result: [],
      pullup: true,
      hasMove: true,
      beforeScroll: true,
      perpage: 20
    };
  },
  computed: {},
  props: {
    query: {
      type: String,
      default: ""
    },
    showSinger: {
      type: Boolean,
      default: true
    }
  },
  components: {
    Scroll,
    Loading,
    noResult
  },
  methods: {
    _search() {
      this.hasMove = true;
      this.page = 1;
      this.$refs.suggest.scrollTo(0, 0);
      search(this.query, this.page, this.showSinger, this.perpage).then(res => {
        if (res.code === ERR_OK) {
          this.result = this._getResult(res.data);
          console.log(res);
          this._checkMove(res.data);
        }
      });
    },
    _checkMove(data) {
      const song = data.song;
      if (
        !song.list.length ||
        song.curnum + song.curpage * 20 > song.totalnum
      ) {
        this.hasMove = false;
      }
    },
    _getResult(data) {
      let ret = [];
      if (data.zhida && data.zhida.singerid) {
        ret.push({ ...data.zhida, ...{ type: TYPE_SINGER } });
      }
      if (data.song) {
        ret = ret.concat(this.normalizeSongs(data.song.list));
      }
      return ret;
    },
    normalizeSongs(list) {
      let ret = [];
      list.forEach(musicData => {
        if (musicData.songid && musicData.albumid) {
          ret.push(createSong(musicData));
        }
      });
      return ret;
    },
    getIconCls(item) {
      if (item.type === TYPE_SINGER) {
        return "icon-mine";
      } else {
        return "icon-music";
      }
    },
    getDiscplayName(item) {
      if (item.type === TYPE_SINGER) {
        return item.singername;
      } else {
        return `${item.name}-${item.singer}`;
      }
    },
    searchMove() {
      if (!this.hasMove) {
        return;
      }
      this.page++;
      search(this.query, this.page, this.showSinger, this.perpage).then(res => {
        if (res.code === ERR_OK) {
          this.result = this.result.concat(this._getResult(res.data));
          this._checkMove(res.data);
        }
      });
    },
    selectItem(item) {
      if (item.type === TYPE_SINGER) {
        const singer = new Singer({
          id: item.singermid,
          name: item.singername
        });
        this.$router.push({
          path: `/search/${singer.id}`
        });
        this.setSinger(singer);
      } else {
        this.insertSong(item);
      }
      this.$emit("select");
    },
    listScroll() {
      this.$emit("listScroll");
    },
    refresh() {
      this.$refs.suggest.refresh();
    },
    ...mapMutations({
      setSinger: "SET_SINGER"
    }),
    ...mapActions(["insertSong"])
  },
  watch: {
    query() {
      this._search();
    }
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.suggest {
  height: 100%;
  overflow: hidden;

  .suggest-list {
    padding: 0 30px;

    .suggest-item {
      display: flex;
      align-items: center;
      padding-bottom: 20px;
    }

    .icon {
      flex: 0 0 30px;
      width: 30px;

      [class^='icon-'] {
        font-size: 14px;
        color: $color-text-d;
      }
    }

    .name {
      flex: 1;
      font-size: $font-size-medium;
      color: $color-text-d;
      overflow: hidden;

      .text {
        no-wrap();
      }
    }
  }

  .no-result-wrapper {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>



