<template>
 <div class="loading">
   <i></i>
   <i></i>
   <i></i>
   <i></i>
   <div class="text">
     {{title}}
   </div>
 </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: '~'
    }
  },
  data() {
    return {};
  },
  components: {}
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable.styl';

.loading {
  // position: absolute;
  width: 100%;
  height: 100%;
  top: 0
  left: 0
  right: 0
  bottom: 0
  transform translate3d(0,50%,0)
  text-align center
  .text {
    color $color-theme
  }

  i {
    display: inline-block;
    width: 8px;
    height: 40px;
    background: $color-theme;
    -webkit-animation: loading 0.8s infinite ease-in-out;
  }
  i:nth-child(2){
    -webkit-animation: loading 0.8s infinite ease-in-out 0.1s;
  }
  i:nth-child(3){
    -webkit-animation: loading 0.8s infinite ease-in-out 0.2s;
  }
  i:nth-child(4){
    -webkit-animation: loading 0.8s infinite ease-in-out 0.3s;
  }

  @keyframes loading {
    0% {
      -webkit-transform: scale(0.3);
      transform: scale(0.3);
    }

    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }
}
</style>



