<template>
 <div class="no-result">
   {{title}}
 </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: " "
    }
  },
  data() {
    return {};
  },
  components: {}
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';

.no-result {
  text-align: center;
  color: $color-theme;
}
</style>



