<template>
 <div class="rank" ref="rank">
   <scroll ref="toplist" :data="topList" class="toplist">
     <ul>
       <li class="item" @click="selectItem(item)" v-for="item in topList">
         <div class="icon">
           <img width="100" height="100" :src="item.picUrl" alt="">
         </div>
         <ul class="songlist">
           <li class="song" v-for="(item,index) in item.songList">
             <span>{{index + 1}}</span>
             <span>{{item.songname}}-{{item.singername}}</span>
           </li>
         </ul>
       </li>
     </ul>
   </scroll>
   <div class="loading-container" v-show="!topList.length">
     <loading></loading>
   </div>
   <router-view></router-view>
 </div>
</template>

<script type="text/ecmascript-6">
import { getTopList } from "api/rank";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import { playListMixin } from "common/js/mixin";
import { mapGetters, mapMutations } from "vuex";
export default {
  mixins: [playListMixin],
  created() {
    this._getTopList();
  },
  data() {
    return {
      topList: []
    };
  },
  components: {
    Scroll,
    Loading
  },
  computed: {
    ...mapGetters(["playing"])
  },
  methods: {
    _getTopList() {
      getTopList().then(res => {
        this.topList = res.data.topList;
      });
    },
    handlePlayList(playList) {
      const bottom = playList.length > 0 ? "60px" : "";
      this.$refs.rank.style.bottom = bottom;
      this.$refs.toplist.refresh();
    },
    selectItem(item) {
      this.$router.push(`rank/${item.id}`);
      this.setTopList(item);
    },
    ...mapMutations({ setTopList: "SET_TOP_LIST" })
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.rank {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;

  .toplist {
    height: 100%;
    overflow: hidden;
    position: relative;

    .item {
      display: flex;
      margin: 0 20px;
      padding-top: 20px;
      height: 100px;

      &:last-child {
        padding-bottom: 20px;
      }

      .icon {
        flex: 0 0 100px;
        width: 100px;
        height: 100px;
      }

      .songlist {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
        height: 100px;
        overflow: hidden;
        background: $color-highlight-background;
        color: $color-text-d;
        font-size: $font-size-small;

        .song {
          no-wrap();
          line-height: 26px;
        }
      }
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>



