<template>
 <div class="search">
   <div class="search-box-wrapper">
     <search-box @query="onQueryChange" ref="searchBox"></search-box>
   </div>
   <div class="shortcut-wrapper" ref="shortcurWrapper" v-show="!query">
     <scroll ref="shortcut" class="shortcut" :data="shortcut">
       <div>
        <div class="hot-key">
          <h1 class="title"></h1>
          <ul>
            <li @click="addQuery(item.k)" class="item" :key="index" v-for="(item,index) in hotkey">
              <span>{{item.k}}</span>
            </li>
          </ul>
        </div>
        <div class="search-history" v-show="searchHistory.length">
          <h2 class="title">
            <span class="text"></span>
            <span @click="showConfirm()" class="clear">
              <i class="icon-clear"></i>
            </span>
          </h2>
          <search-list :searches="searchHistory" @delete="deleteSearchHistory" @select="addQuery"></search-list>
        </div>
      </div>
     </scroll>
   </div>
   <div class="search-result" ref="suggestResult" v-show="query">
    <suggest ref="suggest" @select="saveSearch()" @listScroll="blurInput()" :query="query"></suggest>
   </div>
   <router-view></router-view>
   <confirm @confirm="clearSearchHistory" ref="confirm" :text="text"></confirm>
 </div>
</template>

<script type="text/ecmascript-6">
import SearchBox from "base/search-box/search-box";
import Suggest from "components/suggest/suggest";
import { getHotKey } from "api/search";
import { ERR_OK } from "api/config";
import { mapActions, mapGetters } from "vuex";
import searchList from "base/search-list/search-list";
import Confirm from "base/confirm/confirm";
import Scroll from "base/scroll/scroll";
import { playListMixin, searchMixin } from "common/js/mixin";
export default {
  mixins: [playListMixin, searchMixin],
  data() {
    return {
      hotkey: [],
      text: "?"
    };
  },
  computed: {
    shortcut() {
      return this.hotkey.concat(this.searchHistory);
    }
  },
  components: {
    SearchBox,
    Suggest,
    searchList,
    Confirm,
    Scroll
  },
  methods: {
    _getHotKey() {
      getHotKey().then(res => {
        if (res.code === ERR_OK) {
          this.hotkey = res.data.hotkey.slice(0, 10);
        }
      });
    },
   

    showConfirm() {
      this.$refs.confirm.show();
    },
    handlePlayList(playList) {
      const bottom = this.playList.length > 0 ? "60px" : "";

      this.$refs.shortcurWrapper.style.bottom = bottom;
      this.$refs.shortcut.refresh();

      this.$refs.suggestResult.style.bottom = bottom;
      this.$refs.suggest.refresh();
    },

  },
  created() {
    this._getHotKey();
  },
  watch: {
    query(newVal) {
      if (!newVal) {
        setTimeout(() => {
          this.$refs.shortcut.refresh();
        }, 20);
      }
    }
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.search {
  .search-box-wrapper {
    margin: 20px;
  }

  .shortcut-wrapper {
    position: fixed;
    top: 178px;
    bottom: 0;
    width: 100%;

    .shortcut {
      height: 100%;
      overflow: hidden;

      .hot-key {
        margin: 0 20px 20px 20px;

        .title {
          margin-bottom: 20px;
          font-size: $font-size-medium;
          color: $color-text-l;
        }

        .item {
          display: inline-block;
          padding: 5px 10px;
          margin: 0 20px 10px 0;
          border-radius: 6px;
          background: $color-highlight-background;
          font-size: $font-size-medium;
          color: $color-text-d;
        }
      }

      .search-history {
        position: relative;
        margin: 0 20px;

        .title {
          display: flex;
          align-items: center;
          height: 40px;
          font-size: $font-size-medium;
          color: $color-text-l;

          .text {
            flex: 1;
          }

          .clear {
            extend-click();

            .icon-clear {
              font-size: $font-size-medium;
              color: $color-text-d;
            }
          }
        }
      }
    }
  }

  .search-result {
    position: fixed;
    width: 100%;
    top: 178px;
    bottom: 0;
  }
}
</style>



