<template>
 <transition name="slide">
   <div class="add-song" v-show="showFlag" @click.stop>

     <div class="header">
       <h1 class="title"></h1>
       <div class="close" @click="hide">
         <i class="icon-close"></i>
       </div>
     </div>

     <div class="search-box-wrapper">
       <search-box ref="searchBox" @query="onQueryChange" placeholder=""></search-box>
     </div>

     <div class="shortcut" v-show="!query">
       <switches :switches="switches" @switch="switchItem" :currentIndex="currentIndex"></switches>
       <div class="list-wrapper">
          <scroll ref="songList" v-if="currentIndex===0" class="list-scroll" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
          <scroll ref="searchList" v-if="currentIndex===1" class="list-scroll" :data="searchHistory">
            <div class="list-inner">
              <search-list :searches="searchHistory" @delete="deleteSearchHistory" @select="addQuery"></search-list>
            </div>
          </scroll>
       </div>
     </div>

     <div class="search-result" v-show="query">
       <suggest :query="query" :showSinger="showSinger" @select="selectSuggest" @listScroll="blurInput"></suggest>
     </div>

     <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1</span>
        </div>
     </top-tip>

   </div>
 </transition>
</template>

<script type="text/ecmascript-6">
import SearchBox from "base/search-box/search-box";
import Suggest from "components/suggest/suggest";
import { searchMixin } from "common/js/mixin";
import Switches from "base/switches/switches";
import Scroll from "base/scroll/scroll";
import songList from "base/song-list/song-list";
import { mapGetters, mapActions, mapMutations } from "vuex";
import Song from "common/js/song";
import TopTip from "base/top-tip/top-tip";
import SearchList from "base/search-list/search-list";
export default {
  mixins: [searchMixin],
  data() {
    return {
      showFlag: false,
      showSinger: false,
      currentIndex: 0,
      switches: [
        {
          name: ""
        },
        {
          name: ""
        }
      ]
    };
  },
  components: {
    SearchBox,
    Suggest,
    Switches,
    Scroll,
    songList,
    TopTip,
    SearchList
  },
  methods: {
    show() {
      this.showFlag = true;
      setTimeout(() => {
        if (this.currentIndex === 0) {
          this.$refs.songList.refresh();
        } else {
          this.$refs.searchList.refresh();
        }
      }, 20);
    },
    hide() {
      this.showFlag = false;
    },
    selectSuggest() {
      this.$refs.topTip.show();
      this.saveSearch();
    },
    switchItem(index) {
      this.currentIndex = index;
    },
    selectSong(item, index) {
      if (index !== 0) {
        this.insertSong(new Song(item));
        this.$refs.topTip.show();
      }
    },
    ...mapActions(["insertSong"])
  },
  computed: {
    ...mapGetters(["playHistory", "searchHistory"])
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.add-song {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 200;
  background: $color-background;

  &.slide-enter-active, &.slide-leave-active {
    transition: all 0.3s;
  }

  &.slide-enter, &.slide-leave-to {
    transform: translate3d(100%, 0, 0);
  }

  .header {
    position: relative;
    height: 44px;
    text-align: center;

    .title {
      line-height: 44px;
      font-size: $font-size-large;
      color: $color-text;
    }

    .close {
      position: absolute;
      top: 0;
      right: 8px;

      .icon-close {
        display: block;
        padding: 12px;
        font-size: 20px;
        color: $color-theme;
      }
    }
  }

  .search-box-wrapper {
    margin: 20px;
  }

  .shortcut {
    .list-wrapper {
      position: absolute;
      top: 165px;
      bottom: 0;
      width: 100%;

      .list-scroll {
        height: 100%;
        overflow: hidden;

        .list-inner {
          padding: 20px 30px;
        }
      }
    }
  }

  .search-result {
    position: fixed;
    top: 124px;
    bottom: 0;
    width: 100%;
  }

  .tip-title {
    text-align: center;
    padding: 18px 0;
    font-size: 0;

    .icon-ok {
      font-size: $font-size-medium;
      color: $color-theme;
      margin-right: 4px;
    }

    .text {
      font-size: $font-size-medium;
      color: $color-text;
    }
  }
}
</style>



