<template>
 <div class="recommend" ref="recommend">
   <scroll ref="scroll" class="recommend-content" :data="discList">
     <div>
       <div class="slider-wrapper" v-if="recommends.length">
        <slider>
          <div v-for="(item,index) in recommends" :key="index">
            <a :href="item.linkUrl" @load="loadImg()">
              <img :src="item.picUrl">
            </a>
          </div>
        </slider>
      </div>
      <div class="recommend-list">
        <h1 class="list-title"></h1>
        <ul v-if="discList.length" ref="list">
          <li @click="selectItem(item)" :key="index" class="item" v-for="(item,index) in discList">
            <div class="icon">
              <img width="60" height="60" v-lazy="item.imgurl" :src="item.imgurl" alt="">
            </div>
            <div class="text">
              <h2 class="name" v-html="item.creator.name"></h2>
              <p class="desc" v-html="item.dissname"></p>
            </div>
          </li>
        </ul>
        <loading v-show="!discList.length"></loading>
      </div>
     </div>
   </scroll>
   <router-view></router-view>
 </div>
</template>

<script type="text/ecmascript-6">
import { ERR_OK } from "api/config";
import { getRecommend, getDiscList } from "api/recommend";
import Slider from "base/slider/slider";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import { mapGetters, mapMutations } from "vuex";
import { playListMixin } from "common/js/mixin";

export default {
  mixins: [playListMixin],
  data() {
    return {
      recommends: [],
      discList: []
    };
  },
  components: {
    Slider,
    Scroll,
    Loading
  },
  created() {
    this._getRecommend();
    this._getDiscList();
  },
  methods: {
    _getRecommend() {
      getRecommend().then(res => {
        if (res.code === ERR_OK) {
          this.recommends = res.data.slider;
        }
      });
    },
    handlePlayList(playList) {
      const bottom = playList.length > 0 ? "60px" : "";

      this.$refs.recommend.style.bottom = bottom;
      this.$refs.scroll.refresh();
    },
    _getDiscList() {
      getDiscList().then(res => {
        this.discList = res.data.list;
        // console.log(this.discList);
      });
    },
    loadImg() {
      this.$refs.scroll.refresh();
    },
    selectItem(item) {
      this.$router.push({
        path: `/recommend/${item.dissid}`
      });
      this.setDisc(item);
    },
    ...mapMutations({
      setDisc: "SET_DISC"
    })
  },
  computed: {
    ...mapGetters(["playList"])
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';

.recommend {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;

  .recommend-content {
    height: 100%;
    overflow: hidden;

    .slider-wrapper {
      position: relative;
      width: 100%;
      overflow: hidden;
    }

    .recommend-list {
      position: relative;

      .list-title {
        height: 65px;
        line-height: 65px;
        text-align: center;
        font-size: $font-size-medium;
        color: $color-theme;
      }

      .item {
        display: flex;
        box-sizing: border-box;
        align-items: center;
        padding: 0 20px 20px 20px;
        text-align: left;

        .icon {
          flex: 0 0 60px;
          width: 60px;
          padding-right: 20px;
        }

        .text {
          display: flex;
          flex-direction: column;
          justify-content: center;
          flex: 1;
          line-height: 20px;
          overflow: hidden;
          font-size: $font-size-medium;

          .name {
            margin-bottom: 10px;
            color: $color-text;
          }

          .desc {
            color: $color-text-d;
          }
        }
      }
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>



