<template>
 <div class="search-box">
    <i class="icon-search"></i>
    <input ref="query" v-model="query" :placeholder="placeholder" class="box"/>
    <i v-show="query" @click="clear" class="icon-dismiss"></i>
  </div>
</template>

<script type="text/ecmascript-6">
import { debounce } from "common/js/util";
export default {
  props: {
    placeholder: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      query: ""
    };
  },
  methods: {
    clear() {
      this.query = "";
    },
    setQuery(query) {
      this.query = query;
    },
    blur(){
      this.$refs.query.blur();
    }
  },
  components: {},
  created() {
    this.$watch(
      "query",
      debounce(newQuery => {
        this.$emit("query", newQuery);
      }, 200)
    );
  }
};
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable';

.search-box {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  padding: 0 6px;
  height: 40px;
  background: $color-search-box-color;
  border-radius: 6px;

  .icon-search {
    font-size: 24px;
    color: $color-background;
  }

  .box {
    flex: 1;
    margin: 0 5px;
    line-height: 18px;
    background: $color-highlight-background;
    padding: 3%;
    color: $color-theme;
    font-size: $font-size-medium;
    outline: none;

    &::placeholder {
      color: $color-text-d;
    }

    &::focus {
      border: 0px solid #000;
    }
  }

  .icon-dismiss {
    font-size: 16px;
    color: $color-background;
  }
}
</style>



